@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+KR:wght@300;400;500;700&display=swap);
header {
  position: fixed;
  top: 0;
  width: 100%;
  height: 5rem;
  border-top: 1rem solid var(--black);
  background-color: var(--white);
  z-index: 9;
}

.header-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1100px;
  height: 100%;
  margin: 0 auto;
}

.header-section {
  display: flex;
}

.sns-link {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 2.5rem;
  height: 2.5rem;
  margin-left: 0.5rem;
}

.sns-link path {
  fill: var(--gray);
  transition: fill 0.2s ease-in-out;
}

.sns-link:hover path {
  fill: var(--primary-color);
}

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 12.5rem;
  margin: 0 auto;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.name {
  cursor: pointer;
  text-decoration: underline;
  transition: color 0.2s ease-in-out;
  color: var(--primary-color);
}

.name:hover {
  color: var(--black);
}

.blink {
  animation-name: blink_effect;
  animation-duration: 0.5s;
  animation-iteration-count: infinite;
}

@keyframes blink_effect {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0;
  }
  51% {
    opacity: 1;
  }
  100% {
    opacity: 1;
  }
}

.project-list {
  display: flex;
  flex-wrap: wrap;
  max-width: 1100px;
  margin: 0 auto;
  animation-name: intro;
  animation-duration: 1s;
}

.project {
  position: relative;
  width: calc((100% / 3) - 1rem);
  margin: 0 0.5rem 1.5rem;
  box-shadow: 0 8px 12px -6px rgba(0, 0, 0, 0.24);
  overflow: hidden;
  cursor: pointer;
}

.thumbnail {
  display: flex;
  justify-content: center;
  align-items: center;
  transition: all 0.5s ease-in-out;
}

.project-info {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 1.5rem;
  background-color: rgba(33, 33, 33, 0.9);
  color: var(--white);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  z-index: 5;
  overflow: hidden;
}

.project-title {
  font-weight: 500;
  margin-bottom: 0.5rem;
}

.project-desc {
  font-size: 0.875rem;
  font-weight: 300;
}

.project:hover .project-info {
  opacity: 1;
}

.project:hover .thumbnail {
  transform: scale(1.05);
}

@keyframes intro {
  from {
    opacity: 0;
    transform: translateY(20%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.modal {
  display: flex;
  justify-content: center;
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  z-index: 10;
}

.background {
  position: absolute;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  z-index: 11;
}

.page-container {
  position: absolute;
  width: 840px;
  max-height: 100%;
  padding: 3rem 3rem 5rem;
  background-color: var(--white);
  z-index: 12;
  opacity: 0;
  transform: translateY(10%);
  transition: opacity 0.5s ease-in-out, transform 0.5s ease-in-out;
  overflow: hidden auto;
}

.page-container button {
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.page-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1.25rem;
  width: 100%;
}

.page-title button:hover svg {
  transition: transform 0.2s ease-in-out;
  transform: scale(1.1);
}

.page-section {
  margin-top: 1rem;
  color: var(--dark-gray);
}

.page-section h3 {
  color: var(--black);
  margin-bottom: 0.5rem;
}

.about {
  display: flex;
}

.about div {
  width: calc(100% / 3);
}

.skill {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 1rem;
}

.skill div {
  width: calc(100% / 4);
}

.open {
  opacity: 1;
}

.close {
  opacity: 0;
}

.open-page {
  transform: translateY(0);
}

.image-slider {
  position: relative;
  width: 100%;
  overflow: hidden;
}

.slider {
  display: flex;
  align-items: center;
  width: 100%;
  transition: all 0.3s ease-in-out;
}

.slider-count {
  bottom: 0.25rem;
  left: 50%;
  padding: 0.25rem 0.5rem;
  border-radius: 0.25rem;
  transform: translateX(-50%);
  background-color: rgba(0, 0, 0, 0.3);
  color: var(--white);
  opacity: 0;
  transition: opacity 0.2s ease-in-out;
  z-index: 14;
}

.image-slider:hover .slider-count {
  opacity: 0.9;
}

.slider-count,
.slider-btns {
  position: absolute;
}

.slider-btns {
  width: 100%;
  height: 100%;
  z-index: 15;
}

.slider-btns button {
  position: absolute;
  width: 30%;
  height: 100%;
}

.prev {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  left: 0;
}

.next {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  right: 0;
}

.slider-btns svg {
  width: 1.5rem;
  height: 1.5rem;
}

.use {
  display: flex;
  align-items: center;
}

.used {
  color: var(--primary-color);
  margin-right: 0.5rem;
}

.used,
.date {
  font-size: 0.875rem;
}

.project-link path {
  fill: var(--gray);
  transition: fill 0.2s ease-in-out;
}

.project-link:hover path {
  fill: var(--primary-color);
}

:root {
  --black: #212121;
  --dark-gray: #777777;
  --gray: #bbbbbb;
  --white: #ffffff;
  --primary-color: #cf0a51;
}

* {
  box-sizing: border-box;
}

html,
body,
h1,
h2,
h3,
p {
  margin: 0;
}

body {
  font-family: 'Noto Sans KR', sans-serif;
  font-size: 16px;
  background-color: #ffffff;
  background-color: var(--white);
  color: #212121;
  color: var(--black);
  overflow: hidden auto;
}

img {
  width: 100%;
}

h1 {
  font-size: 2.25rem;
}

h2 {
  font-size: 1.5rem;
}

h3 {
  font-size: 1.125rem;
}

a {
  color: inherit;
  text-decoration: inherit;
}

main {
  max-width: 1100px;
  padding-top: 5rem;
  margin: 0 auto;
}

